import React from "react";
import { useNavigate } from "react-router-dom";
import "./Sidebar.css";

// 
import logo from "./logo.svg";
import icon1 from "./icon1.svg";
import icon2 from "./icon2.svg";
import icon3 from "./icon3.svg";
import icon4 from "./icon4.svg";

//   (   URL )
const menuItems = [
  {
    label: "HOME",
    children: [
      { label: "", icon: icon1, route: "/user/CustomerMainPage" },
    ],
  },
  {
    label: "  ",
    children: [
      { label: " ", icon: icon1, route: "/user/CustomerEstimate" },
      { label: " ", icon: icon1, route: "/user/CustomerContract" },
      { label: " ", icon: icon1, route: "/user/CustomerInbound" },
    ],
  },
  {
    label: "",
    children: [
      { label: " ", icon: icon2, route: "/user/CustomerOutboundRequest" },
      { label: " ", icon: icon2, route: "/user/CustomerOutbound" },
    ],
  },
  {
    label: "",
    children: [
      { label: "", icon: icon3, route: "/user/Notices" },
      { label: "", icon: icon4, route: "/user/Inquiries" },
      { label: "", icon: icon4, route: "/user/Employees" },
    ],
  },
];

// Sidebar 
const Sidebar = () => {
  const navigate = useNavigate();

  return (
    <div className="sidebar">
      {/*   */}
      <div className="sidebar-header">
        <img src={logo} alt="Logo" className="sidebar-logo" />
        Smart WMS
      </div>

      {/*   */}
      {menuItems.map((menu, index) => (
        <div key={index} className="menu-group">
          <div className="menu-label">{menu.label}</div>
          <div className="menu-children">
            {menu.children.map((child, childIndex) => (
              <button
                key={childIndex}
                className="sidebar-item"
                onClick={() => navigate(child.route)}
              >
                {child.icon && (
                  <img
                    className="sidebar-icon"
                    src={child.icon}
                    alt={child.label}
                  />
                )}
                <span>{child.label}</span>
              </button>
            ))}
          </div>
        </div>
      ))}
    </div>
  );
};

export default Sidebar;
